import type {AxeResults, Result} from 'axe-core'
import {clearErrors, logError} from './helpers'

const helpText = `\nReach out to @github/accessibility or slack #accessibility for help`
const reported: WeakMap<HTMLElement, string[]> = new WeakMap()

export function processError(violation: Result) {
  for (const node of violation.nodes) {
    if (!node.element) continue
    const element = node.element
    const errors = reported.get(element) || []
    if (errors.indexOf(violation.id) >= 0) continue

    errors.push(violation.id)
    reported.set(element, errors)
    if (!violation.impact || isAxeFalsePositive(violation)) continue

    const messages = [violation.description, violation.helpUrl]

    if (violation.id === 'link-in-text-block') {
      messages.push(
        "\nATTENTION: Links in text blocks should now respect the user's accessibility setting. See https://thehub.github.com/news/2023-10-18-a-more-readable-github/#what-you--need-to-know for guidance.",
      )
    }

    const duplicateMainMessage =
      "\nDuplicate <main> issues involving Primer React's PageLayout can be resolved by setting `as='div'` on the PageLayout.Content\n"
    if (violation.id === 'landmark-unique') {
      for (const violationNode of violation.nodes) {
        if (violationNode.element?.tagName === 'MAIN') {
          messages.push(duplicateMainMessage)
        }
      }
    }
    if (violation.id === 'landmark-main-is-top-level' || violation.id === 'landmark-no-duplicate-main') {
      messages.push(duplicateMainMessage)
    }

    if (node.failureSummary) {
      messages.push(node.failureSummary)
    }

    messages.push(helpText)
    logError({
      name: `[${violation.impact[0]!.toUpperCase()}${violation.impact.slice(1)}] ${violation.help}`,
      message: messages.join('\n'),
      element,
    })
  }
}

function isAxeFalsePositive(violation: Result): boolean {
  if (violation.id === 'aria-required-children') {
    // There's currently false positives in axe-core where lists are much too strict about their allowed child types
    // (tracked in https://github.com/dequelabs/axe-core/issues/3439) and `aria-hidden` children of lists not being
    // ignored in checks (tracked in https://github.com/dequelabs/axe-core/issues/3850). We'll deal with it by
    // ignoring false alarms raised by usage of the `ActionList` component, which contains a DOM structure which
    // triggers these issues, for now.
    for (const node of violation.nodes) {
      if (node.element?.classList?.contains('ActionList')) return true
    }
  }
  return false
}

export function consoleReporter(err: Error, results: AxeResults) {
  if (err) {
    // eslint-disable-next-line no-console
    console.error(err)
    return
  }

  for (const incomplete of results.incomplete) {
    if (incomplete.impact !== 'serious' && incomplete.impact !== 'critical') continue
  }
  clearErrors()
  for (const violation of results.violations) {
    processError(violation)
  }
}
