document.addEventListener(
  'error',
  e => {
    if (!(e.target instanceof HTMLScriptElement)) return

    const errorBanner = document.querySelector<HTMLElement>('#bundle-error-notice')
    if (!errorBanner) return

    errorBanner.parentElement?.setAttribute('data-catalyst-inline', 'true')
    errorBanner.hidden = false

    const output = errorBanner.querySelector<HTMLElement>('.js-bundle-error-names')
    if (!output) return

    const existing = output.textContent?.trim() || ''

    const srcParts = e.target.src.split('/')
    const filename = srcParts[srcParts.length - 1]!

    if (existing.length === 0) {
      output.textContent = filename
    } else {
      output.textContent += `, ${filename}`
    }
  },
  {capture: true},
)
