import safeStorage from '@github-ui/safe-storage'
import {isTurboFrame} from '@github-ui/turbo/utils'

// highlight swapped in turbo frames in development to make debugging easier
document.addEventListener('turbo:frame-render', event => {
  const {getItem} = safeStorage('localStorage')
  const debug = getItem('turbo.debug')

  if (!debug) {
    return
  }

  const frame = event.target as HTMLElement
  if (isTurboFrame(frame)) {
    const originalStyle = frame.style.outlineStyle
    frame.style.outlineStyle = 'solid'
    setTimeout(() => {
      frame.style.outlineStyle = originalStyle
    }, 2000)
  }
})
