interface CustomError {
  element: HTMLElement
  name: string
  message: string
}

function applyErrorCount(errorContainer: Element) {
  const errorCounter = errorContainer.querySelector<HTMLElement>('.js-console-error-count')!
  errorCounter.textContent = document.querySelectorAll('.console-error').length.toString()
}

export function logError(error: CustomError) {
  const errorContainer = document.querySelector<HTMLElement>('#console-error-notice')!
  errorContainer.parentElement?.setAttribute('data-catalyst-inline', 'true')
  errorContainer.hidden = false

  outputToConsole(error)
  error.element.classList.add('console-error')
  error.element.addEventListener('click', function () {
    if (isInteractiveElement(error.element)) {
      // Avoid repeated blocking of interactions.
      if (!error.element.hasAttribute('data-accessibility-alert')) {
        alert(`${error.name}. See console for more details.`)
        error.element.setAttribute('data-accessibility-alert', 'true')
      }
    } else {
      alert(`${error.name}. See console for more details.`)
    }
    outputToConsole(error)
  })

  applyErrorCount(errorContainer)
}

function outputToConsole(error: CustomError) {
  const errorMessage = `${error.name}\n${error.message}`
  // eslint-disable-next-line no-console
  console.warn(errorMessage)
  // eslint-disable-next-line no-console
  console.log(error.element)
}

function isInteractiveElement(element: HTMLElement) {
  return element.matches(
    'input, a[href], select, textarea, button, [role="menuitemradio"], [role="menuitemcheckbox"], [role="menuitem"], summary',
  )
}

export function clearErrors() {
  const errorContainer = document.querySelector<HTMLElement>('#console-error-notice')!
  if (!document.querySelector('.console-error')) {
    errorContainer.hidden = true
  }
  applyErrorCount(errorContainer)
}
