import {controller, target} from '@github/catalyst'
import {setCookie, deleteCookie, getCookie} from '@github-ui/cookies'
import DOMPurify from 'dompurify'

@controller
class TenantSelectorElement extends HTMLElement {
  @target currentTenant: HTMLButtonElement
  @target details: HTMLElement

  connectedCallback() {
    const currentTenant = getCookie('gh_tenant')
    if (currentTenant) {
      this.setCurrentButtonText(currentTenant.value)
    }
  }

  select(data: PointerEvent) {
    if (!data.currentTarget) {
      return
    }
    const newTenantValue = this.getTenantFromElement(data.currentTarget as HTMLElement)

    // That's default tenant, just clear the cookie
    if (newTenantValue === 'github-inc') {
      deleteCookie('gh_tenant')

      // eslint-disable-next-line i18n-text/no-en
      this.setCurrentButtonText('Select tenant')
      this.details.removeAttribute('open')
      return
    }

    setCookie('gh_tenant', newTenantValue)
    this.setCurrentButtonText(newTenantValue)
    this.details.removeAttribute('open')
  }

  private getTenantFromElement(element: HTMLElement) {
    return element?.textContent?.replaceAll('\n', '').replaceAll('&lt;', '<').replaceAll('&gt;', '>').trim() || ''
  }

  private setCurrentButtonText(text: string) {
    const currentText = this.getTenantFromElement(this.currentTenant)
    const newHtml = this.currentTenant.innerHTML.replace(currentText, text)
    this.currentTenant.innerHTML = DOMPurify.sanitize(newHtml, {USE_PROFILES: {html: true}})
  }
}
