import type {AxeResults, RuleObject} from 'axe-core'
import axe from 'axe-core'
import customRules from '../axe-custom-rules'
import {getCookie} from '@github-ui/cookies'
import {loaded} from '@github-ui/document-ready'
import {consoleReporter} from './accessibility-scan'

type Reporter = (err: Error, results: AxeResults) => void

const reporters: Reporter[] = []

// Rules that are slow-performing and noisy that we don't want enabled by default in this scanning setup.
export const rulesToNeverEnable = ['color-contrast']

// Rules that we eventually enable in this scanning setup.
const rulesToEventuallyEnable = [
  'page-has-heading-one', // Noisy (on every page) and non-trivial to fix
]

function configureRules(searchParam: URLSearchParams, rules: string[]) {
  const config: RuleObject = {}

  for (const rule of rules) {
    config[rule] = {enabled: searchParam.has(rule) || searchParam.has('strict-axe-check')}
  }
  return config
}

function addReporter(reporter: Reporter) {
  reporters.push(reporter)
}

async function attachScanTriggers() {
  await loaded

  window.requestIdleCallback(function () {
    scanForProblems(document)
  })

  document.addEventListener('turbo:load', () => {
    scanForProblems(document)
  })

  function instanceOfToggleEvent(event: Event): event is ToggleEvent {
    return 'newState' in event
  }

  document.addEventListener(
    'toggle',
    function (event: Event) {
      if (!(event.target instanceof HTMLElement)) return
      const details = event.target.closest('details')
      if (details && details.hasAttribute('open')) {
        window.requestIdleCallback(function () {
          scanForProblems(details)
        })
      }

      // Check for popovers opening
      if (!instanceOfToggleEvent(event)) return
      if (event.newState === 'open') {
        window.requestIdleCallback(function () {
          scanForProblems(event.target as HTMLElement)
        })
      }
    },
    {capture: true},
  )
}

/**
 * Please reach out to us in slack #accessibility or @github/accessibility so
 * that we can continue to improve this developer experience.
 * If you must, you can disable the scan using the human-shaped toggle button with the label "Disable accessibility axe scan" in the staff bar.
 * Run the aXe scan at least once before submitting your PR to avoid introducing accessibility issues.
 */
function scanForProblems(context: Document | Element | string) {
  if (scanning) return // https://github.com/dequelabs/axe-core/issues/1041
  const searchParams = new URLSearchParams(window.location.search)
  scanning = true
  const {scrollY} = window

  const options: axe.RunOptions = {
    elementRef: true,
    resultTypes: ['violations'],
    rules: {
      ...configureRules(searchParams, rulesToNeverEnable),
      ...configureRules(searchParams, rulesToEventuallyEnable),
    },
  }
  axe.run(
    {
      include: [context],
    },
    options,
    (err: Error, results: AxeResults) => {
      scanning = false
      window.scroll(0, scrollY)
      for (const reporter of reporters) {
        reporter(err, results)
      }
    },
  )
}
axe.configure(customRules)

let scanning = false
const isAccessibilityScanningEnabled = getCookie('accessibilityScan')?.value !== 'false'
if (isAccessibilityScanningEnabled) {
  addReporter(consoleReporter)
  attachScanTriggers()
}
